export default {
  data() {
    return {
      selectedLocale: this.$i18n.locale,
      availableLocales: [
        {
          code: "en",
          name: "English",
          flag: "🇬🇧",
          iso: "en-GB"
        },
        {
          code: "de",
          name: "Deutsch",
          flag: "🇩🇪",
          iso: "de-DE",
        },
        {
          code: "es",
          name: "Español",
          flag: "🇪🇸",
          iso: "es-ES",
        },
      ],
    };
  },
  watch: {
    selectedLocale(newLocale) {
      this.$router.push(this.switchLocalePath(newLocale));
    }
  }
};
