export default {
  data() {
    return {
      valid: true,
      name: "",
      nameRules: [(v) => !!v || this.$t("name")],
      email: "",
      emailRules: [
        (v) => !!v || this.$t("email"),
        (v) => /.+@.+\..+/.test(v) || this.$t("email")
      ],
      text: "",
      starValue: 0,
      starRules: [(v) => !!v || this.$t("rating")],
      select: null,
      dialog: false,
      message: null
    };
  },

  methods: {
    validate() {
      const valid = this.$refs.form.validate();
      if (valid) {
        this.valid = false;
        this.$fire.firestore
          .collection("messages")
          .doc()
          .set({
            name: this.name,
            email: this.email,
            text: this.text,
            rating: this.starValue,
            locale: this.$i18n.locale,
            created: this.$fireModule.firestore.FieldValue.serverTimestamp()
          })
          .then(() => {
            this.message = this.$t("messageReceived");
          });
      }
    }
  }
};
